import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../../../environments/environment';
import { YeuCauSuDungPTNModel } from '../../../../../shared/models/yeu-cau-su-dung-ptn.model';
@Injectable({
  providedIn: 'root'
})
export class YeuCauSuDungPtnService {
  constructor(private httpClient: HttpClient) { }

  getAllYeuCauSuDungPTN(): Observable<any> {
    let url = environment.api.url + 'YeuCauSuDungPTN/getAll';
    return this.httpClient.get(url);
  }
  approve(yeuCau: YeuCauSuDungPTNModel, token: string): Observable<any> {
    let url = environment.api.url + 'YeuCauSuDungPTN/approve'
    return this.httpClient.post(url, yeuCau)
  }

}
