import { Component, OnInit, Inject } from '@angular/core';
import {
  MatDialogRef,
  MAT_DIALOG_DATA,
  MatDialog,
} from '@angular/material/dialog';
import { HuyYeuCauService } from './huy-yeu-cau.service';
import { ThietBiChiTietModel } from '../../../../../../shared/models/thiet-bi-chi-tiet.model';
import { AlertDialogComponent } from '../../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { DatePipe } from '@angular/common';
import { YeuCauSuDungPTNModel } from '../../../../../../shared/models/yeu-cau-su-dung-ptn.model';
import { Router } from '@angular/router';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-huy-yeu-cau',
  templateUrl: './huy-yeu-cau.component.html',
  styleUrls: ['./huy-yeu-cau.component.scss'],
})
export class HuyYeuCauComponent implements OnInit {
  _yeuCau = new YeuCauSuDungPTNModel();
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogMainRef: MatDialogRef<HuyYeuCauComponent>,
    private __service: HuyYeuCauService,
    public dialog: MatDialog,
    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __router: Router,
    public datepipe: DatePipe
  ) {}
  _title = 'Hủy yêu cầu';
  _titleMainButton = 'Hủy yêu cầu';
  _thietBiChiTiet: ThietBiChiTietModel[];
  showProgressBar = false;
  ngOnInit(): void {
    if (this.data.yeuCau) {
      this._yeuCau = this.data.yeuCau;
    }
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  approve() {
    if (this._yeuCau.GhiChu.trim() == '') {
      this.__snackbar.open('Vui lòng nhập đủ thông tin', null, {
        duration: 2000,
      });
    } else {
      this.showProgressBar = true;
      this.__service.huyYeuCau(this._yeuCau.Id, this._yeuCau.GhiChu).subscribe(
        (data) => {
          this.showProgressBar = false;
          if (data === 'true') {
            this.__snackbar.open(`Đã hủy yêu cầu ${this._yeuCau.Id}`, null, {
              duration: 2000,
            });
            this.dialogMainRef.close({
              result: true,
            });
          } else {
            this.__snackbar.open(data, null, {
              duration: 2000,
            });
          }
        },
        (error) => {
          this.dialogMainRef.close({
            result: false,
          });
          this.showLogin();
          this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
            duration: 2000,
          });
        }
      );
    }
  }
  cancel() {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: 'Thay đổi của bạn sẽ không được lưu. Bạn có muốn hủy?',
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.dialogMainRef.close({
          result: false,
        });
      }
    });
  }
}
