import {
  AfterViewInit,
  ChangeDetectorRef,
  Component,
  OnInit,
  ViewChild,
} from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatTableDataSource } from '@angular/material/table';
import { MatSort } from '@angular/material/sort';
import { MatPaginator } from '@angular/material/paginator';
import { YeuCauSuDungPtnService } from './quan-ly-yeu-cau-su-dung-ptn.service';
import { AlertDialogComponent } from '../../../../../shared/pages/alert-dialog/alert-dialog.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { HuyYeuCauComponent } from './huy-yeu-cau/huy-yeu-cau.component';
// import {
//   LichSuMuonThietBiModel,
//   MuonThietBiReqModel,
// } from 'src/app/models/lich-su-muon-thiet-bi.model';
import {
  eMenuAdmin,
  environment,
} from '../../../../../../environments/environment';
import { UserModel } from '../../../../../shared/models/user.model';
import { YeuCauSuDungPTNModel } from '../../../../../shared/models/yeu-cau-su-dung-ptn.model';
import { flatMap } from 'rxjs/operators';
import { MainAdminComponent } from '../../main-nav.component';
import { Router } from '@angular/router';
@Component({
  selector: 'app-yeu-cau-su-dung-ptn',
  templateUrl: './quan-ly-yeu-cau-su-dung-ptn.component.html',
  styleUrls: ['./quan-ly-yeu-cau-su-dung-ptn.component.scss'],
})
export class QuanLyYeuCauSuDungPTNComponent implements OnInit {
  _length = 0;
  _user = new UserModel('', '', '', '', '', '', '');
  displayedColumns: string[] = [
    'HoTen',
    'NgaySinh',
    'MaSo',
    'LoaiHocVien',
    'SDT',
    'Email',
    'TenDeTai',
    'TenGVHD',
    'ThoiGian',
    'TenHVCH_NCS',
    'MaHVCH_NCS',
    'IsCancel',
    'TenNguoiHuy',
    'NgayHuy',
    'actions',
  ];

  @ViewChild(MatPaginator) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  constructor(
    public dialog: MatDialog,
    private __service: YeuCauSuDungPtnService,
    private _alertDialog: MatDialog,
    private __snackbar: MatSnackBar,
    private __router: Router,
    private __mainComponent: MainAdminComponent
  ) {
    localStorage.setItem(
      environment.key_storage.MENU.ADMIN,
      eMenuAdmin[eMenuAdmin.YEU_CAU_SU_DUNG_PTN]
    );
    this.__mainComponent.__localStorage = localStorage.getItem(
      environment.key_storage.MENU.ADMIN
    );
  }

  _yeuCaus: YeuCauSuDungPTNModel[] = [];
  dataSource: MatTableDataSource<YeuCauSuDungPTNModel>;
  showProgressBar = true;
  __today = new Date().getTime();
  ngOnInit(): void {
    this.getDataYeuCaus();
  }

  private getDataYeuCaus() {
    this.showProgressBar = true;
    this.__service.getAllYeuCauSuDungPTN().subscribe(
      (data) => {
        this.showProgressBar = false;
        if(data.Message){
          this.__snackbar.open(data.Message, null, {
            duration: 2000,
          });
          return
        }
        this._yeuCaus = data;
        this._yeuCaus.forEach((f) => {
          f.NgayBatDauDate = new Date(f.ThoiGianBatDau);
          f.NgayKetThucDate = new Date(f.ThoiGianKetThuc);
          if (f.NgayHuy) f.NgayHuyDate = new Date(f.NgayHuy);
          f.IsCancel = f.NguoiHuy != null;
          if (f.NgayKetThucDate.getTime() <= this.__today) f.IsOver = true;
          else f.IsOver = false;
        });
        this.dataSource = new MatTableDataSource(this._yeuCaus);

        this._length = this.dataSource.data.length;
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.dataSource.filterPredicate = (
          data: YeuCauSuDungPTNModel,
          filter: string
        ) =>
          data.HoTen.toLocaleLowerCase().indexOf(filter) != -1 ||
          data.MaSo.toLocaleLowerCase().indexOf(filter) != -1;

      },
      (error) => {
        this.showProgressBar = false;
        this.showLogin();
        this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
          duration: 2000,
        });
      }
    );
  }

  applyFilter(filterValue: string) {
    filterValue = filterValue.trim(); // Remove whitespace
    filterValue = filterValue.toLowerCase(); // Datasource defaults to lowercase matches
    this.dataSource.filter = filterValue;
  }

  approve(yeuCau: YeuCauSuDungPTNModel) {
    var dialogRef = this._alertDialog.open(AlertDialogComponent, {
      disableClose: true,
      width: '17rem',
      data: {
        dataKey: `Bạn có chắc muốn duyệt yêu cầu sử dụng phòng thí nghiệm của người học ${yeuCau.HoTen}?`,
        isConfirm: true,
      },
    });
    dialogRef.afterClosed().subscribe((res) => {
      if (res) {
        this.showProgressBar = true;
        this.__service
          .approve(
            yeuCau,
            localStorage.getItem(environment.key_storage.TOKEN.ADMIN)
          )
          .subscribe(
            (data) => {
              this.showProgressBar = false;
              if (data === 'true') {
                this.__snackbar.open('Cập nhật thành công', null, {
                  duration: 2000,
                });
                this.getDataYeuCaus();
              } else {
                this.__snackbar.open(data, null, {
                  duration: 2000,
                });
              }
            },
            (error) => {
              this.showProgressBar = false;
              this.showLogin();
              this.__snackbar.open(environment.message.TOKEN_EXPIRED, null, {
                duration: 2000,
              });
            }
          );
      }
    });
  }
  cancel(yeuCau: YeuCauSuDungPTNModel) {
    const confirmDialogRef = this.dialog.open(HuyYeuCauComponent, {
      disableClose: true,
      width: '40rem',
      data: {
        yeuCau: yeuCau,
      },
    });
    confirmDialogRef.afterClosed().subscribe((res) => {
      if (res.result) {
        this.getDataYeuCaus();
      }
    });
  }
  showLogin() {
    this.__router.navigate(['admin/login']);
  }
  onSearchChange(searchText: String) {
    this.dataSource.filter = searchText.trim().toLowerCase();
  }
}
